.overlay__screen3 {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.65);
  z-index: 1;
  width: 100%;
  height: 100%;
}

.gallery__body {
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  height: 1080px;
}
@media (max-width: 768px) {
  .gallery__body {
    height: inherit;
    max-height: 1080px;
  }
}

.gallery__title {
  position: absolute;
  top: 455px;
  color: white;
  z-index: 5;
  cursor: pointer;
  font-size: 36px;
  line-height: 100%;
  font-weight: 400;
  font-family: "Bruno Ace SC";
  left: 50%;
  transform: translate(-50%);
}

.gallery__card {
  transition: 1.5s ease;
  cursor: pointer;
  position: relative;
  max-width: 640px;
  overflow: hidden;
}
.gallery__card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}
.gallery__card:hover .overlay__screen3 {
  background: rgba(0, 0, 0, 0.5);
}
.gallery__card:hover .gallery__title {
  color: #ffac30;
  left: 17.6%;
  transform: translate(-50%);
}
.gallery__card:active .gallery__title {
  color: #ffac30;
}
.gallery__card:hover {
  width: 980px;
  max-width: 980px;
}
.gallery__card:active {
  width: 980px;
  max-width: 980px;
}
.gallery__card:hover .gallery__info {
  opacity: 1;
  visibility: visible;
}
.gallery__card:hover .gallary__path-color {
  opacity: 1;
}
.gallery__card:hover .gallary__title-color {
  fill: #ffac30;
}
@media (max-width: 768px) {
  .gallery__card:hover .gallery__title {
    color: #ffac30;
    left: 50%;
    transform: translate(-50%);
  }
  .gallery__card:active .gallery__title {
    color: #ffac30;
    left: 50%;
    transform: translate(-50%);
  }
}

.elite__body img {
  object-position: left 10px bottom 20px;
  transform: scale(1.5);
}
@media (max-width: 768px) {
  .elite__body img {
    object-position: center;
    transform: scale(1.1);
  }
}

.extra__body img {
  transform: scale(1.14);
  object-position: center;
}
@media (max-width: 768px) {
  .extra__body img {
    object-position: center;
    transform: scale(1);
  }
}

@media (max-width: 768px) {
  .vip__body img {
    object-position: center;
  }
}

@media (max-width: 768px) {
  .vip__body {
    position: relative;
  }
}

.vip__body::after {
  position: absolute;
  content: "";
  width: 2px;
  height: 150%;
  background: linear-gradient(rgba(255, 255, 255, 0), #ffffff, rgba(255, 255, 255, 0));
  right: 0;
  top: 0;
  z-index: 5;
}
@media (max-width: 768px) {
  .vip__body::after {
    height: 2px;
    width: 100%;
    background: linear-gradient(to right, rgba(255, 255, 255, 0), #ffffff, rgba(255, 255, 255, 0));
  }
}

.vip__body::before {
  position: absolute;
  content: "";
  width: 2px;
  height: 150%;
  background: linear-gradient(rgba(255, 255, 255, 0), #ffffff, rgba(255, 255, 255, 0));
  left: 0;
  top: 0;
  z-index: 5;
}
@media (max-width: 768px) {
  .vip__body::before {
    height: 2px;
    width: 100%;
    top: inherit;
    bottom: 0;
    background: linear-gradient(to right, rgba(255, 255, 255, 0), #ffffff, rgba(255, 255, 255, 0));
  }
}

@media (max-width: 768px) {
  .extra__body::after {
    position: absolute;
    content: "";
    width: 2px;
    height: 150%;
    background: linear-gradient(rgba(255, 255, 255, 0), #ffffff, rgba(255, 255, 255, 0));
    left: 0;
    top: 0;
    z-index: 5;
  }
}
@media (max-width: 768px) and (max-width: 768px) {
  .extra__body::after {
    height: 2px;
    width: 100%;
    top: inherit;
    bottom: 0;
    background: linear-gradient(to right, rgba(255, 255, 255, 0), #ffffff, rgba(255, 255, 255, 0));
  }
}

